










































































































import { Component, Vue } from "vue-property-decorator";
import Banner from "../components/Banner.vue";
import {
  BRow,
  BCol,
  BContainer,
  BLink,
  BToast,
  BOverlay,
  BButton,
  BForm,
  BFormInput,
  BFormGroup,
} from "bootstrap-vue";
import { ApiService } from "../services/api";
import "bootstrap-vue";

@Component({
  components: {
    BRow,
    BCol,
    BContainer,
    BLink,
    BToast,
    BOverlay,
    BButton,
    BFormInput,
    BFormGroup,
    BForm,
    Banner,
  },
})
export default class Home extends Vue {
  public username = "";
  public password = "";
  public isLoading = false;

  public async onSubmit(event: Event) {
    event.preventDefault();
    if (this.isInvalidForm()) {
      this.$bvToast.show("invalidForm");
    } else {
      this.isLoading = true;
      try {
        const loginCallback = await ApiService.login({
          username: this.username,
          password: this.password,
        });
        window.location.replace(loginCallback.url);
      } catch (error) {
        this.$bvToast.show("invalidForm");
      }
      this.isLoading = false;
    }
  }

  private isInvalidForm(): boolean {
    return this.isBlank(this.username) && this.isBlank(this.password);
  }

  private isBlank(value: string): boolean {
    if (!value) return true;
    else if (value.length === 0 || value.trim().length === 0) return true;
    return false;
  }
}
